import React from "react";
import { PayloadAction, createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import { api } from "@services/index";
import { IGeneralStateData } from "@models/index";

interface IGeneralStateDataAdding {
  title: string;
  prices: string;
  body: string;
  discount: string;
  fileUploaded: File;
}
const toBase64 = (file: File): unknown =>
  new Promise((resolve, reject) => {
    console.log("file", file);
    const reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = () => resolve(reader.result);
    reader.onerror = reject;
  });
export const addingBook = createAsyncThunk<unknown, IGeneralStateDataAdding>(
  "book/add",
  async function ({ title, prices, discount, body, fileUploaded }) {
    try {
      const response = await api.post(`data`, {
        title: title,
        price: prices,
        discount: discount,
        image: String(await toBase64(fileUploaded)),
        body: body,
      });
      return { title, body, fileUploaded, prices, discount };
    } catch (err: any) {
      console.log("err", err);
      throw new Error(err);
    }
  }
);

const initialState: IGeneralStateData = {
  loading: true,
  error: "",
  isSuccess: false,
};
export const bookAddingSlice = createSlice({
  name: "books",
  initialState,
  reducers: {
    changeAddingState: (state) => {
      state.isSuccess = false;
    },
  },
  extraReducers: (builder) => {
    builder
      .addCase(addingBook.pending, (state: IGeneralStateData) => {
        console.log("pending");
        state.loading = true;
      })
      .addCase(
        addingBook.fulfilled,
        (state: IGeneralStateData, action: PayloadAction<any>) => {
          state.data?.push(action.payload);
          state.loading = false;
          state.isSuccess = true;
        }
      )
      .addCase(
        addingBook.rejected,
        (state: IGeneralStateData, action: PayloadAction<any>) => {
          state.error = action.payload;
          console.log(state.error, "error");
          state.loading = false;
        }
      );
  },
});

export default bookAddingSlice.reducer;
export const { changeAddingState } = bookAddingSlice.actions;
