//@ts-nocheck
import React, { useState, useEffect } from "react";
import { Formik, Form, Field } from "formik";
import { addingSchema } from "@utils/index";
import { IValidationSchemeValues } from "@models/index";
import style from "./update.module.css";
import { useNavigate } from "react-router-dom";
import { useAppDispatch, useAppSelector } from "@hooks/index";
import { TextField, Input } from "@mui/material";
import {
  changeUpdatingState,
  updateingBook,
} from "@store/slices/requests/updatinDataRequest";

const UpdateingBookInfo = () => {
  const navigate = useNavigate();
  const dispatch = useAppDispatch();
  const isSuccess = useAppSelector((state) => state.updatingData);
  const data = useAppSelector((state) => state.gettingData);

  useEffect(() => {
    if (isSuccess) {
      dispatch(updateingBook());
      dispatch(changeUpdatingState());
    }
  }, [isSuccess]);
  useEffect(() => {
    console.log(data);
    if (!data) {
      dispatch(gettingBooksData());
    }
  }, [data]);
  const [fileUploaded, serFileUploaded] = useState<File | string | undefined>();

  const MuiTextField = ({ field, form, ...props }) => {
    // console.log(typeof props, "types");
    return <TextField {...field} {...props} />;
  };
  const MuiInputField = ({ field, form, ...props }) => {
    // console.log(typeof props, "types");
    return <Input {...field} {...props} />;
  };
  return (
    <div className={style.parent_div}>
      <Formik
        validateOnMount={true}
        initialValues={{
          title: data.title,
          body: data.body,
          prices: data.prices,
          discount: data.discount,
          image: data.image,
        }}
        // validationSchema={addingSchema}
        onSubmit={(values: IValidationSchemeValues, { resetForm }) => {
          console.log(values, fileUploaded, " validation");
          //   resetForm();

          dispatch(updateingBook({ ...values, fileUploaded }));
          navigate("/");
        }}
      >
        {({ errors, touched, isSubmitting, isValid }) => (
          <Form>
            <div className={style.parent}>
              <div className={style.titleBodyPositions}>
                <Field
                  name="title"
                  type="text"
                  component={MuiTextField}
                  variant="outlined"
                  autoComplete="off"
                  label="Title *"
                  className={style.inputField}
                />
                {errors.title && touched.title ? (
                  <div className={style.error}>{errors.title}</div>
                ) : null}
                {fileUploaded && (
                  <img
                    src={URL.createObjectURL(fileUploaded)}
                    alt=""
                    className={style.uploadedImg}
                  />
                )}
                <Field
                  name="image"
                  component={MuiInputField}
                  variant="outlined"
                  id="file"
                  type="file"
                  onChange={(event) => {
                    console.log(event.currentTarget.files[0]);
                    serFileUploaded(event.currentTarget.files[0]);
                  }}
                />

                <Field
                  name="body"
                  type="text"
                  multiline
                  rows={10}
                  component={MuiTextField}
                  variant="outlined"
                  autoComplete="off"
                  label="Description *"
                  className={style.inputField}
                />
                {errors.body && touched.body ? (
                  <div className={style.error}>{errors.body}</div>
                ) : null}
              </div>
              <div className={style.pricesDisCount}>
                <Field
                  name="prices"
                  component={MuiTextField}
                  type="text"
                  variant="outlined"
                  autoComplete="off"
                  label="Price *"
                  className={style.inputFieldPrice}
                />
                {errors.prices && touched.prices ? (
                  <div className={style.error}>{errors.body}</div>
                ) : null}
                <Field
                  name="discount"
                  type="text"
                  component={MuiTextField}
                  variant="outlined"
                  autoComplete="off"
                  label="Discount *"
                  className={style.inputFieldPrice}
                />
                {errors.discount && touched.discount ? (
                  <div className={style.error}>{errors.discount}</div>
                ) : null}
              </div>
            </div>
            {/* <div className={style.fixing_button}> */}
            <button
              className={style.submitButton}
              type="submit"
              //   disabled={isSubmitting || !{ isValid }}
            >
              Save
            </button>
            {/* </div> */}
          </Form>
        )}
      </Formik>
    </div>
  );
};

export default UpdateingBookInfo;
